<template>
  <div class="slide-1 center-content">
    <div
      class="img-wrapper"
    >
      <img
        v-if="!slideContent.acf.slider_video"
        :src="slideContent.acf.slider_image.url"
        :alt="slideContent.acf.slider_image.title"
      />

      <video
        v-if="
          slideContent.acf.slider_video && slideContent.acf.slider_video.url
        "
        autoplay
        muted
        loop
        playsinline
      >
        <source :src="slideContent.acf.slider_video.url" />
        Your browser does not support the video tag.
      </video>
    </div>
    <h3 v-for="(text, i) in toAr(slideContent.acf.slider_sub_title)" :key="i">
      <div class="text-wrapper">
        <span v-if="text.length" v-html="text"></span>
      </div>
    </h3>
  </div>
</template>

<script>
export default {
  props: [
    'slideContent'
  ],
  name: 'slide1',
  methods: {
    toAr (str) {
      if (str) {
        return str.split('<br />')
      }
    }
  }
}
</script>

<style lang="scss">
.main-swiper {
  .slide-1 {
    height: 100%;
    h3 {
      cursor: pointer;
      width: 100%;
      margin-bottom: 0;
      &:nth-child(3) {
        padding: 0;
        background: none;

        &::before,
        &::after {
          content: "";
          display: block;
          // margin-bottom: 1rem;
          width: 100%;
          height: 1px;
          background-image: linear-gradient(
            to right,
            rgba($color: #fff, $alpha: 0.5) 15%,
            rgba(255, 255, 255, 0) 0%
          );
          background-position: bottom;
          background-size: 5px 1px;
          background-repeat: repeat-x;
        }

        // &:hover {
        //   &::before {
        //     margin: 1rem;
        //   }
        // }
      }
    }
    .text-wrapper {
      transition: all 0.6s cubic-bezier(0.16, 1, 0.3, 1);
      width: 100%;
      padding: 1rem 0;
      margin: 0;
      text-align: center;

      span {
        max-width: 300px;
      }
      &:hover {
        padding: 3rem 0;
        margin: 1rem 0;
        background: rgba($color: #000000, $alpha: 0.4);
      }
    }
  }

  .swiper-slide {
